import SmokeMachine from '@bijection/smoke'

function UseSmoke() {
  var canvas = document.getElementById('canvas')
  var ctx = canvas.getContext('2d')
  canvas.width = 800
  canvas.height = 800

  var party = SmokeMachine(ctx, [192, 192, 192]) // 设置烟雾颜色为银灰色

  party.start() // start animating

  var density = 3; // 设置初始烟雾密度
  var timer = setInterval(function () {
    if (density > 0) {
      party.addSmoke(Math.random() * canvas.width, Math.random() * canvas.height, density);
      //density -= 1;
    } else {
      clearInterval(timer); // 停止计时器
    }
  }, 100); // 每300毫秒新增一次烟雾，直到密度为0为止
}

export { UseSmoke };
